<template>
  <div>
    <el-button type="danger" size="mini" round @click="handleDelete"
      >删除</el-button
    >
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  computed: {
    // 选中菜单
    currentName() {
      return this.$store.state.ItemDetails.itemDate;
    },
    curIndex() {
      return this.$store.state.ItemDetails.itemIndex;
    },
    // 二级全部菜单
    navList() {
      return this.$store.state.ItemDetails.menuList;
    },
  },
  methods: {
    getdeleteData() {
      const that = this;
      that.$api
        .deleteData({ dataSetName: this.currentName })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleDelete() {
      console.log("开始删除");
      console.log(this.currentName);
      const that = this;

      this.$confirm("此操作将永久删除该数据集, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          that.$api
            .deleteData({ dataSetName: this.currentName })
            .then((res) => {
              console.log(res);
              this.$store.commit(
                "ItemDetails/delect_resultList_isDelect",
                false
              );
              this.$store.commit(
                "ItemDetails/delect_resultList_isDelect",
                true
              );
              this.$message({
                type: "success",
                message: "删除成功!",
              });
            })
            .catch((err) => {
              console.log(err);
              this.$message({
                type: "error",
                message: "删除失败",
              });
            });

          // console.log(this.navList);
          // var dataSet = this.navList.filter((item) => {
          //   return item.dataSetName != this.currentName;
          // });

          // console.log(dataSet);
          // console.log(typeof dataSet);
          // console.log(this.currentName, this.curIndex);
          // this.navList.splice(this.currentName, 1);
          // console.log(dataSet);
          // this.$store.commit("ItemDetails/deleteData", dataSet);
          // this.getdeleteData();
        })
        .catch((err) => {
          console.log(err);
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
};
</script>
