<template>
  <div>
    <el-button
      type="primary"
      size="mini"
      round
      @click="handleUpload"
      @closed="handleClose"
      >上传</el-button
    >
    <el-dialog
      title="上传数据"
      :close-on-click-modal="false"
      :visible.sync="dialogFormVisible"
    >
      <!-- <el-radio-group v-model="mode" @change="changeMode">
        <el-radio label="1">上传图片</el-radio>
        <el-radio label="2">上传文件夹</el-radio>
      </el-radio-group> -->
      <el-upload
        class="upload-demo"
        ref="upload"
        action="#"
        :http-request="httpReq"
        :before-upload="beforeUpload"
        :on-error="uploadError"
        :auto-upload="false"
        :limit="3000"
        multiple
      >
        <!-- 添加的进度条 -->
        <!-- <el-progress
          v-if="showprocess"
          type="line"
          :percentage="progressPercent"
          style="margin-top: 10px"
        ></el-progress> -->
        <div class="el-upload__bts">
          <el-button slot="trigger" size="small" type="primary"
            >选取文件</el-button
          >
          <el-button
            style="margin-left: 10px"
            size="small"
            type="success"
            @click="submitUpload"
            >上传到服务器</el-button
          >
        </div>
        <div slot="tip" class="el-upload__tip" v-if="currentType == 'text'">
          只能上传csv文件，且不超过20M
        </div>
        <div
          slot="tip"
          class="el-upload__tip"
          v-else-if="currentType == 'audio'"
        >
          只能上传mp3文件，且不超过20M
        </div>
        <div
          slot="tip"
          class="el-upload__tip"
          v-else-if="currentType == 'video'"
        >
          只能上传mp4文件，且不超过1200M
        </div>
      </el-upload>

      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="hideUpload">取 消</el-button>
        <el-button type="primary" size="mini" @click="determineUpload"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 当前数据集名称
      currentDateName: "",
      // 选中上传方式
      mode: "1",
      // 控制上传弹窗显隐
      dialogFormVisible: false,
      // 图片上传时的参数
      postImgParam: {
        datasetname: "",
        action: "",
        version: "",
        index: "",
      },
      // 文件类型和大小的限制
      type: "",
      size: "",
      code: "",
      // 进度条
      showprocess: false,

      progressPercent: 0,
      timer: "",
    };
  },
  computed: {
    // 二级导航栏的选中值
    currentName() {
      return this.$store.state.ItemDetails.itemDate;
    },
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
  },
  watch: {
    // 监听二级导航栏的选中值
    currentName: {
      handler(newValue, oldValue) {
        console.log(newValue, oldValue);
        // this.currentDateName = newValue;
        // console.log(this.currentDateName, newValue);
      },
    },
  },
  methods: {
    uploadchange(file, fileList) {
      console.log(file);
      if (file.status === "ready") {
        this.percentlength = 0;
        this.showprocess = true;
        const timer = setInterval(() => {
          if (this.percentlength >= 99) {
            clearInterval(timer);
            return;
          }
          this.percentlength += 1;
        }, 20);
      }
      if (file.status === "success") {
        this.percentlength = 100;
        this.showprocess = false;
      }
    },

    // 上传方式
    // changeMode() {
    //   console.log("上传的方式变化了" + this.mode);
    // },
    // 点击上传按钮
    handleUpload() {
      console.log("点击上传");
      this.dialogFormVisible = true;
    },
    beforeUpload(file) {
      let testfile = file.name.substring(file.name.lastIndexOf(".") + 1);
      console.log(testfile);
      if (this.currentType == "video") {
        this.type = testfile === "mp4"; // 限制文件类型
        this.size = file.size / 1024 / 1024 < 1200; // 限制文件大小
        if (!this.type) {
          this.$message({
            message: "文件类型只能是 mp4格式!",
            type: "warning",
          });
        }
        if (!this.size) {
          this.$message({
            message: "文件大小不能超过1200MB!",
            type: "warning",
          });
        }
        return this.type && this.size;
        // return this.type;
      } else if (this.currentType == "text") {
        this.type = testfile === "csv"; // 限制文件类型
        this.size = file.size / 1024 / 1024 < 10; // 限制文件大小
        if (!this.type) {
          this.$message({
            message: "文件类型只能是csv 格式!",
            type: "warning",
          });
        }
        // if (!this.size) {
        //   this.$message({
        //     message: "文件大小不能超过10MB!",
        //     type: "warning",
        //   });
        // }
        // return this.type && this.size;
        return this.type;
      } else if (this.currentType == "audio") {
        this.type = testfile === "mp3"; // 限制文件类型
        this.size = file.size / 1024 / 1024 < 10; // 限制文件大小
        if (!this.type) {
          this.$message({
            message: "文件类型只能是mp3格式!",
            type: "warning",
          });
        }
        // if (!this.size) {
        //   this.$message({
        //     message: "文件大小不能超过10MB!",
        //     type: "warning",
        //   });
        // }
        // return this.type && this.size;
        return this.type;
      }
    },
    // 手动上传图片时,执行的上传方法
    httpReq(param) {
      // if (this.mode == 1) {
      console.log("httpReq");
      console.log(param);
      console.log(this.currentDateName);
      this.currentDateName = this.currentName;
      // const that = this;
      this.showprocess = true;
      this.progressPercent = 0;
      // const config = {
      //   onUploadProgress: (progressEvent) => {
      //     // progressEvent.loaded:已上传文件大小
      //     // progressEvent.total:被上传文件的总大小
      //     this.progressPercent = Number(
      //       ((progressEvent.loaded / progressEvent.total) * 100).toFixed(2)
      //     );
      //   },
      // };

      let fileObj = param.file; // 相当于input里取得的files
      let fd = new FormData(); // FormData 对象
      console.log(param.file.type, param.file.size);
      fd.append("file", fileObj); // 文件对象
      fd.append("dataSetName", this.currentDateName); //数据集名称
      this.$api.uploadImgs(fd).then((res) => {
        console.log(res);
        this.code = res.code;
        if (this.progressPercent < 100) {
          this.progressPercent++;
        }
        if (this.progressPercent == 100) {
          this.showprocess = false;
          this.progressPercent = 0;
        }

        this.$refs.upload.uploadFiles = this.$refs.upload.uploadFiles.filter(
          (item) => {
            console.log(item);
            console.log(param.file.name);
            return param.file.name != item.name;
          }
        );
      });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    // 手动上传到服务器
    submitUpload(val) {
      console.log("执行了");
      console.log(val);
      this.$refs.upload.submit();

      // this.$emit("getPageData");
    },
    // 上传失败
    uploadError(file, fileList) {
      console.log(fileList);
    },
    // 对话框关闭回调事件的触发
    handleClose() {},
    // 点击弹窗取消按钮
    hideUpload() {
      const that = this;
      that.$refs.upload.uploadFiles = [];
      that.dialogFormVisible = false;
    },
    // 点弹窗确定按钮
    determineUpload() {
      const that = this;
      console.log(that.$refs.upload.uploadFiles.length);
      if (that.$refs.upload.uploadFiles.length > 0) {
        that.$message({
          type: "error",
          message: "部分图片上传失败",
        });
      } else if (!this.type) {
        that.$message({
          type: "warning",
          message: "重新上传文件类型",
        });
      } else if (!this.size) {
        that.$message({
          type: "warning",
          message: "上传文件过大",
        });
      } else if (this.code == 200) {
        that.$message({
          type: "success",
          message: "上传成功",
        });
      }
      that.$refs.upload.uploadFiles = [];
      that.dialogFormVisible = false;
      this.$emit("changePageData", this.currentName);
    },
  },
};
</script>
<style lang="scss" scoped>
// .el-upload__bts {
//   margin: 10px 0;
// }
/deep/ .el-dialog__body {
  padding: 0px 20px;
}
.el-upload__tip {
  color: red;
}
</style>
